.guarantee-page{
  padding: 2.75em 0 5em;
}

.guarantee-main{
  min-height: 22.75em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 3.75em;
  padding-left: 24.375em;
  font-family: $font2;
  position: relative;
  &__img{
    position: absolute;
    top: -3.125em;
    left: -5.75em;
    width: 32.625em;
    pointer-events: none;
  }
  &__title{
    font-size: 1.125em;
    line-height: 182%;
    font-weight: 700;
    span{
      padding: 0 1em;
      background: #9de386;
      border-radius: .78em;
    }
  }
  &__desc{
    font-size: 1em;
    line-height: 182%;
    font-weight: 500;
    margin-top: 1.5em;
  }
}

.guarantee-main,
.advices{
  &__title{
    font-size: 1.875em;
    line-height: 123%;
    font-weight: 600;
    font-family: $font2;
  }
}

.guarantee-mean{
  margin-top: 6em;
  &__items{
    margin-top: 2.75em;
    @include grid100gap(.625em);
  }
}

.guarantee-item{
  padding: 3em 4.5em 3.5em 8.875em;
  background: #fff;
  border-radius: 2.5em;
  position: relative;
  &__num{
    border-radius: 2.5em 0;
    width: 6em;
    height: 5.5625em;
    background: #9de386;
    @include center;
    position: absolute;
    top: 0;
    left: 0;
    font-weight: 700;
    font-family: $font2;
    color: #fff;
    span{
      font-size: 1.5em;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 172%;
    font-weight: 500;
    span{
      font-weight: 700;
    }
  }
}

.guarantee-hint{
  margin: 3.875em auto 0;
  width: 48.25em;
  max-width: 100%;
  height: 15.25em;
  position: relative;
  padding: 5.125em 1.875em 2.875em 12.8125em;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 182%;
    font-weight: 500;
    font-family: $font2;
    max-width: 30.5em;
  }
  &__img2{
    position: absolute;
    width: 13.875em;
    top: 2.5em;
    left: -.5em;
  }
}


@media screen and (max-width: 992px){
  .guarantee-page{
    padding: 1.5em 0 2em;
  }

  .guarantee-main{
    font-size: .75em;
    &__title{
      span{
        -webkit-box-decoration-break: clone;
        box-decoration-break: clone;
        padding: 0 .5em;
      }
      br{
        display: none;
      }
    }
  }

  .guarantee-mean,
  .advices{
    &__title{
      font-size: 1.5em;
      max-width: 90%;
    }
  }

  .guarantee-mean{
    margin-top: 3em;
    &__items{
      margin-top: 2.625em;
      gap: 2.125em;
    }
  }

  .guarantee-item{
    padding: 2.75em .9375em 2.125em;
    border-radius: 1.25em;
    &__num{
      width: 2.375em;
      height: 2.375em;
      left: .9375em;
      top: -1.1875em;
      border-radius: .5em;
      span{
        font-size: 1em;
      }
    }
    &__text{
      font-size: .875em;
      line-height: 155%;
    }
  }

  .guarantee-hint{
    margin-top: 1.75em;
  }
}


@media screen and (max-width: 576px){
  .guarantee-main{
    margin-top: 2.125em;
    font-size: 1em;
    padding: 0;
    &__img{
      width: calc(100% - 1.25em);
      display: block;
      position: static;
    }
    &__title{
      font-size: 1em;
      line-height: 157%;
    }
    &__desc{
      margin-top: -2.5em;
    }
  }

  .guarantee-hint{
    height: 23.9375em;
    padding: 5.4375em 1.375em 9.125em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__text{
      line-height: 153%;
    }
    &__img2{
      top: unset;
      bottom: -4.3125em;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
