.lifehack-page,
.guide-page{
  &__desc{
    font-size: 1.125em;
    line-height: 122%;
    font-family: $font2;
    font-weight: 600;
    margin-top: 1.22em;
  }
}

.guide-page{
  padding: 2.75em 0 5em;
  &__desc{
    max-width: 30em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.75em));
    gap: 2.75em 3.5em;
    margin-top: 3.75em;
  }
}

.guide-item{
  display: flex;
  &__icon{
    width: 5.125em;
    height: 5.125em;
    flex-shrink: 0;
    border-radius: 50%;
    @include center;
    box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.09);
    background-color: $yellow;
    border: .25em solid #fff;
    margin-right: 1.75em;
    img{
      max-width: 3em;
      max-height: 3em;
    }
  }
  &__content{
    padding-top: .8125em;
  }
  &__text{
    line-height: 162%;
    font-size: 1em;
    b{
      font-weight: 600;
    }
  }
}

.guide-hint{
  margin-top: 5em;
  border: .4375em solid #fff;
  min-height: 12.875em;
  padding: 2.75em 2.5em 2.5em 17.5em;
  border-radius: 2.5em;
  font-weight: 700;
  font-family: $font2;
  position: relative;
  &__img{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 2.3125em;
    width: 11.75em;
  }
  &__title{
    font-size: 1.25em;
    line-height: 151%;
    color: $primary;
  }
  &__desc{
    margin-top: .75em;
    font-size: 1.25em;
    line-height: 167%;
  }
}


@media screen and (max-width: 992px){
  .guide-page,
  .lifehack-page{
    font-weight: 500;
    font-size: 1em;
    line-height: 157%;
  }

  .guide-page{
    padding: 1.5em 0 2em;
    &__desc{
      margin-top: 1.5em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .875em));
      gap: 1.75em;
      margin-top: 2.5em;
    }
  }

  .guide-item{
    display: flex;
    &__icon{
      width: 2.8125em;
      height: 2.8125em;
      border-width: .125em;
      margin-right: 1.3125em;
      img{
        max-width: 1.625em;
        max-height: 1.625em;
      }
    }
    &__content{
      padding-top: 0;
    }
    &__text{
      line-height: 155%;
      font-size: .875em;
    }
  }

  .guide-hint{
    margin-top: 4.625em;
    padding: 4em 1.25em 1.25em;
    border-radius: 1.25em;
    &__title{
      font-size: 1.125em;
      line-height: 147%;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
      margin-top: .875em;
    }
    &__img{
      width: 7.5em;
      transform: unset;
      top: -3em;
      left: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .guide-page{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
