.error-page{
  flex-grow: 1;
  padding: 2em;
  @include center;
  flex-direction: column;
  &__img{
    width: 75em;
    max-width: 100%;
  }
  &__link{
    font-size: 1.5em;
    line-height: 125%;
    text-decoration: underline;
    font-weight: 700;
    margin-top: -2.5em;
  }
}


@media screen and (max-width: 992px){
  .error-page{
    padding: 1em;
    &__link{
      margin-top: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .error-page{
    &__link{
      font-size: 1.25em;
    }
  }
}

