.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 39em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 4em;
        border-radius: 2.1875em;
        display: inline-block;
    }
    &__img{
        position: absolute;
        top: -2.5em;
        left: -3.5em;
        width: 11.625em;
    }
    &__img2{
        left: -1em;
        top: -4em;
        width: 8.5em;
    }
    &__img3{
        top: 1em;
        width: 12.5em;
    }
    &__title{
        font-size: 1.875em;
        line-height: 123%;
        font-family: $font2;
        font-weight: 600;
        text-align: center;
    }
    &__desc{
        font-size: 1em;
        line-height: 120%;
        text-align: center;
        font-weight: 500;
        margin-top: .5em;
    }
    .close-btn{
        width: 1em;
        @include center;
        position: absolute;
        top: 1.5em;
        right: 1.5em;
        &:hover{
            path{
                fill: $black;
            }
        }
        svg{
            width: 100%;
        }
    }
    &__wrap{
        width: 23.125em;
        max-width: 100%;
        margin: 2.375em auto 0;
    }
    &__nav{
        @include grid100gap(.6875em);
    }
}

.modal-form{
    &__fields{
        @include grid100gap(.375em);
    }
    &__nav{
        margin-top: 2.375em;
        @include grid100gap(1.125em);
    }
    &__btn{
        width: 100%;
    }
    &__btn2{
        display: inline-flex;
        margin: 0 auto;
        font-weight: 500;
        span{
            font-size: 1em;
            line-height: 120%;
        }
    }
    &__files{
        @include grid100gap(.6875em);
        margin-top: 1.25em;
    }
    &__code{
        margin-top: .5em;
        font-size: 1em;
        line-height: 119%;
        font-weight: 500;
        &:hover{
            color: $primary;
        }
    }
}

.modal-radios{
    &__item{
        padding-bottom: 1.125em;
        @include lastM(0 0 1.125em);
        border-bottom: 1px solid #e7e7e7;
    }
}

.modal-bonus{
    margin-top: 1.6875em;
    padding-bottom: 1.875em;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &__wrap{
        display: flex;
        align-items: center;
        padding: 0 1.125em;
        background: linear-gradient(162deg, #000 0%, #606060 100%);
        height: 2.8125em;
        border-radius: .75em;
    }
    img{
        width: 1.5625em;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
        border-radius: 50%;
        margin-right: .5em;
    }
    &__num{
        color: #d6d6d6;
        font-size: 1.125em;
        font-weight: 500;
    }
    &__desc{
        position: absolute;
        left: 0;
        bottom: 0;
        font-size: 1em;
        line-height: 119%;
        color: #6d6d6d;
    }
}

.bonuses{
    margin-top: 1.6875em;
    @include grid100gap(.8125em);
}

.bonus-row{
    display: flex;
    justify-content: space-between;
    padding-bottom: .8125em;
    border-bottom: 1px solid #ddd;
    &__date{
        font-size: 1.25em;
        line-height: 120%;
    }
    &__id{
        color: #6d6d6d;
        font-size: .875em;
        line-height: 130%;
    }
    &__col2{
        padding-top: .5625em;
    }
    &__change{
        font-size: 1.25em;
        line-height: 125%;
        font-weight: 600;
        color: #1bac43;
        &._red{
            color: $primary;
        }
    }
}

.claim-modal{
    .modal__title{
        text-align: left;
    }
    &__desc{
        font-size: 1em;
        line-height: 150%;
        color: #6d6d6d;
        margin-top: 1.25em;
        b{
            font-weight: 600;
        }
    }
    .modal__wrap{
        width: 100%;
    }
}

.about-modal{
    .modal__dialog{
        width: 75em;
        background-color: $yellow;
        padding: 5em 6.5em 6.25em 5.3125em;
    }
    .close-btn{
        width: 2em;
        top: 2.75em;
        right: 2.25em;
    }
    &__grid{
        display: grid;
        grid-template-columns: 25em calc(100% - 28.75em);
        gap: 1em;
        justify-content: space-between;
    }
    &__col2{
        padding-top: 1em;
    }
    &__title{
        font-size: 5.0625em;
        line-height: 93%;
        font-weight: 600;
        font-family: $font2;
    }
    &__subtitle{
        font-size: 1.25em;
        line-height: 164%;
        font-weight: 700;
        color: rgba(0, 0, 0, 0.8);
    }
    &__desc{
        font-size: 1.125em;
        line-height: 130%;
        color: rgba(0, 0, 0, 0.8);
        margin-top: 1em;
    }
}

.order-modal{
    .modal__wrap{
        width: 100%;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
        &__img{
            display: none;
        }
    }

    .about-modal{
        .modal__dialog{
            padding: 2em;
        }
        .close-btn{
            width: 1em;
            top: .9375em;
            right: .75em;
        }
        &__grid{
            grid-template-columns: 100%;
        }
        &__col2{
            padding-top: 0;
        }
        &__title{
            font-size: 2.5em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &:after{
            display: none;
        }
        &__dialog {
            max-width: 100%;
            width: 100%;
            height: $vh;
            border-radius: 0;
            padding: 4em 1.1875em 2em;
            overflow-y: auto;
        }
        &__title{
            font-size: 1.5em;
        }
        &__desc{
            font-size: .875em;
        }
        .close-btn{
            top: .9375em;
            right: .75em;
        }
        &__wrap{
            margin-top: 1.625em;
        }
    }

    .modal-form{
        &__nav{
            margin-top: 1.625em;
            gap: .875em;
        }
        &__files{
            margin-top: .6875em;
        }
    }

    .modal-bonus{
        margin-top: 1.75em;
        padding-bottom: 0;
        &__wrap{
            width: 100%;
            padding: 1em 1.125em;
            height: auto;
            border-radius: .5625em;
        }
        img{
            width: 2.5625em;
            margin-right: 1.125em;
        }
        &__num{
            color: #fff;
            line-height: 167%;
            font-weight: 600;
        }
        &__desc{
            position: static;
            font-size: .875em;
            color: #fff;
        }
    }

    .bonuses{
        margin-top: 2em;
        gap: .625em;
    }

    .bonus-row{
        padding-bottom: .625em;
        &__date{
            font-size: 1em;
        }
        &__change{
            font-size: 1.125em;
        }
    }

    .claim-modal{
        &__desc{
            font-size: .875em;
            margin-top: 1em;
        }
    }

    .about-modal{
        .modal__dialog{
            padding: 4em 1.1875em 2em;
        }
        &__grid{
            font-size: .875em;
        }
    }
}
