.lk{
  padding: 3em 0 5em;
}

.lk-main{
  min-height: 16.25em;
  box-shadow: 0 12px 19px 0 rgba(0, 0, 0, 0.11);
  background: linear-gradient(161deg, #000 0%, #787878 100%);
  color: #fff;
  border-radius: 2.5em;
  position: relative;
  overflow: hidden;
  padding: 1.5em 2em 2em 9em;
  &:before{
    content: '';
    position: absolute;
    width: 9.1875em;
    height: 8.5em;
    top: 0;
    left: -.5em;
    background-image: url(../img/circles.svg);
    @include bgImgSize(100%);
  }
  &__user{
    position: absolute;
    top: 2.0625em;
    left: 2.375em;
    width: 2.125em;
  }
  &__name{
    font-size: 1.875em;
    line-height: 123%;
    font-weight: 600;
    font-family: $font2;
    margin-left: 1em;
    max-width: 11em;
  }
  &__edit{
    position: absolute;
    width: 1.5em;
    @include center;
    top: 1.75em;
    right: 1.75em;
    img{
      width: 100%;
    }
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 2em;
  }
  &__items{
    display: flex;
  }
  &__item{
    @include lastM(0 3.125em 0 0);
    &-title{
      font-size: 1em;
      line-height: 167%;
      color: #787878;
      margin-bottom: .125em;
    }
    &-desc{
      font-weight: 500;
      font-size: 1.125em;
      line-height: 167%;
      color: #d6d6d6
    }
  }
  &__items2{
    display: flex;
    color: #d6d6d6;
    font-weight: 500;

  }
  &__status{
    display: flex;
    align-items: center;
    img{
      width: 1.5625em;
      margin-right: .75em;
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
      border-radius: 50%;
    }
  }
  &__bonus{
    display: flex;
    align-items: center;
    margin-left: 2.5em;
    padding: 0 1.125em;
    height: 2.8125em;
    background: rgba(255, 255, 255, 0.12);
    border-radius: .75em;
    img{
      width: 1.5625em;
      margin-right: .5em;
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
      border-radius: 50%;
    }
  }
}

.lk-orders{
  margin-top: 2.5em;
  .tab{
    padding: 0 2.375em;
    height: 3.125em;
    border-radius: .75em;
    font-weight: 700;
    font-family: $font1;
    border: 1px solid #757575;
    background-color: transparent;
    &._active{
      background: linear-gradient(162deg, #000 0%, #606060 100%);
      color: #fff;
    }
    span{
      font-size: 1em;
    }
  }
  .tab-content{
    margin-top: 2.5em;
  }
}

.orders{
  @media screen and (min-width: 993px){
    font-size: calc(1500px * (16 / 1500));
    @media screen and (max-width: 1500px){
      font-size: calc(100vw * (16 / 1500));
    }
    .container{
      max-width: 1500px;
    }
  }
}

.orders-table{
  &__grid{
    display: grid;
    grid-template-columns: 10.5em 11.875em 8.125em 6.875em 11.125em 6.125em 15.625em 9.25em calc(100% - 79.5em);
  }
}

.orders-head{
  margin-bottom: 2.5em;
  &__col{
    padding-left: 1.5em;
    &:first-child{
      padding-left: 2.25em;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 600;
  }
}

.orders-list{
  @include grid100gap(.75em);
}

.order-row{
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.05);
  background: #fff;
  border-radius: .75em;
  border: 1px solid #fff;
  transition: all .3s;
  position: relative;
  &._active{
    border-color: $primary;
    .order-row__btn{
      opacity: 1;
      pointer-events: unset;
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__btn{
    background-color: transparent;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 2.25em;
    height: 2.25em;
    svg{
      width: .75em;
    }
    @media screen and (min-width: 993px){
      opacity: 0;
      pointer-events: none;
    }
  }
  &__show{
    padding: .5em 0;
    cursor: pointer;
  }
  &__hidden{
    padding: 0 2.1875em 1.5625em;
    display: none;
  }
  &__col{
    padding-left: 1.5em;
    padding-right: .75em;
    border-right: 1px dashed #e6e6e6;
    display: flex;
    align-items: center;
    min-height: 2.5em;
    &:first-child{
      padding-left: 2.25em;
    }
    &:last-child{
      border-right: none;
    }
  }
  &__title{
    display: none;
  }
  &__desc{
    width: 100%;
    .text16{
      line-height: 119%;
    }
  }
  &__address{
    width: 100%;
    position: relative;
    height: 2.5em;
    display: flex;
    align-items: center;
    &:hover{
      cursor: pointer;
      .order-row__address-tooltip{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &-tooltip{
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: -1px;
      background: rgba(0, 0, 0, 0.73);
      padding: .875em 1.75em;
      border: 1px solid #000;
      border-radius: .9375em;
      backdrop-filter: blur(13px);
      white-space: nowrap;
      font-weight: 500;
      color: #fff;
    }
    .text16{
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
  &__status{
    color: #1bac43;
    &._red{
      color: $primary;
    }
    &._yellow{
      color: #f49b0c;
    }
  }
  &__time{
    display: flex;
    flex-direction: column;
  }
}


@media screen and (max-width: 992px){
  .lk{
    padding: 1.5em 0 2em;
  }

  .lk-main{
    &__bottom{
      display: block;
    }
    &__items2{
      margin-top: 1.5em;
    }
  }

  .lk-orders{
    margin-top: 1.75em;
    .tab-content{
      margin-top: 1.875em;
    }
  }

  .orders-head{
    display: none;
  }

  .order-row{
    padding: 1.25em .9375em;
    overflow: hidden;
    &__btn{
      padding: .75em 1em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 4.8125em;
        height: 4.8125em;
        border-radius: 50%;
        background-color: $primary;
      }
      svg{
        position: relative;
        z-index: 2;
      }
      path{
        fill: #fff;
      }
    }
    &__show{
      padding: 0;
      grid-template-columns: 100%;
      gap: .75em;
      padding-bottom: 1.25em;
    }
    &__hidden{
      padding: 0;
    }
    &__col{
      display: grid;
      grid-template-columns: 9em calc(100% - 9em);
      align-items: unset;
      padding: 0 !important;
      min-height: unset;
      border: none;
    }
    &__title{
      font-size: 1em;
      line-height: 119%;
      display: block;
      font-weight: 600;
    }
    &__address{
      height: unset;
    }
    &__time{
      flex-direction: row;
      span{
        margin-right: 1em;
      }
    }
    &__beton{
      display: flex;
      .text14,
      .text20{
        font-size: 1em;
        line-height: 119%;
        font-weight: 400;
        margin-right: .25em;
      }
    }
    &__price{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .lk-main{
    padding: 1.25em .9375em 2.375em;
    &:before{
      background-image: url(../img/circles2.svg);
      width: 6.375em;
      height: 5.25em;
    }
    &__user{
      width: 1.5em;
      top: 1.125em;
      left: 1.75em;
    }
    &__name{
      font-size: 1.25em;
      padding-left: 4em;
      max-width: unset;
    }
    &__items{
      display: block;
      padding: 1.125em 0 1.5em;
      border-top: 1px solid rgba(255, 255, 255, 0.16);
      border-bottom: 1px solid rgba(255, 255, 255, 0.16);
    }
    &__item{
      @include lastM(0 0 .5em);
      display: flex;
      align-items: center;
      &-title{
        margin: 0 1em 0 0;
      }
    }
    &__items2{
      flex-direction: column;
      align-items: flex-start;
    }
    &__bonus{
      margin: 1.125em 0 0;
    }
    &__edit{
      width: 1.875em;
      height: 1.875em;
      background: rgba(255, 255, 255, 0.12);
      border-radius: .75em;
      top: 1.125em;
      right: 1em;
      img{
        width: 1em;
      }
    }
  }

  .lk-orders{
    .tab{
      padding: 0 .25em;
      height: 3em;
      @include lastM(0 .5em 0 0);
      flex-grow: 1;
      justify-content: center;
      span{
        font-size: .875em;
      }
    }
  }

  .order-row{
    &__col{
      grid-template-columns: 7.5em calc(100% - 7.5em);
    }
    .text16,
    &__time,
    &__title{
      font-size: .875em;
    }
    &__beton{
      .text14,
      .text20{
        font-size: .875em;
      }
    }
    &__price{
      font-size: .875em;
    }
  }
}
