.quiz-manufacture{
  &__range{
    max-width: 46.375em;
    .range-field__top{
      display: flex;
      justify-content: space-between;
    }
    .range-field__val{
      display: none;
    }
    .range-field__from,
    .range-field__to{
      font-size: 1.5em;
      line-height: 121%;
      font-weight: 600;
      font-family: $font2;
    }
    .range-slider{
      height: .375em;
      border-radius: 0;
      margin-top: 1.375em;
      .noUi-handle{
        width: 1.25em;
        height: 1.25em;
        top: -.4375em !important;
        right: -.4375em !important;
      }
    }
  }
  &__wrap{
    margin-top: 2.25em;
    display: grid;
    grid-template-columns: calc(100% - 15.375em - 2.75em) 15.375em;
    gap: 1.75em;
  }
}

.manufactures{
  position: relative;
  padding-right: 3.25em;
  overflow-y: auto;
  max-height: 20em;
  &::-webkit-scrollbar {
    width: 0.25em;
    height: 0.25em;
  }
  &::-webkit-scrollbar-track {
    background: #e6e6e6;
    border-radius: .25em;
  }
  &::-webkit-scrollbar-thumb {
    background: #c3c3c3;
    border-radius: .25em;
  }
  &__items{

    //padding-top: 3.75em;

  }
}

.manufactures-head{
  background-color: #F6F6F6;
  display: grid;
  grid-template-columns: 13.5em 10.375em 14.5em calc(100% - 38.375em);
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  padding-right: 3.25em;
  z-index: 2;
  &__col{
    min-height: 3.75em;
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    border-right: 1px solid #e6e6e6;
    &:last-child{
      border-right: none;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 600;
    color: #939393;
  }
}

.manufacture-item{
  input:checked{
    @media screen and (min-width: 577px){
      &~.manufacture-item__grid{
        background-color: #fff;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.05);
        .manufacture-item__col{
          border-color: transparent;
        }
      }
    }
  }
  &__grid{
    min-height: 3.75em;
    border-radius: .75em;
    transition: all .3s;
    display: grid;
    grid-template-columns: 13.5em 10.375em 14.5em calc(100% - 38.375em);
  }
  &__col{
    padding: .75em 1.25em;
    border-right: 1px solid #e6e6e6;
    transition: all .3s;
    display: flex;
    align-items: center;
    &:last-child{
      border-right: none;
    }
  }
  &__col4{
    padding-right: 0;
  }
  &__img{
    height: 2em;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__price,
  &__num{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 600;
  }
}

.manufacture-props{
  @include grid100gap(1.5em);
}

.manufacture-prop{
  &._active{
    .manufacture-prop__show{
      color: $primary;
    }
    .manufacture-prop__btn{
      svg{
        transform: rotate(-180deg);
      }
      path{
        stroke: $primary;
      }
    }
    .manufacture-prop__icon{
      path{
        fill: $primary;
      }
    }
  }
  &__show{
    transition: all .3s;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  &__icon{
    width: 1.5em;
    margin-right: .4375em;
    order: -1;
  }
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 500;
    order: -1;
  }
  &__icon2{
    width: .6875em;
    display: block;
    cursor: pointer;
  }
  .m-tooltip{
    align-self: flex-start;
    margin-top: -.1875em;
    margin-left: -.1875em;
  }
  &__btn{
    margin-left: auto;
    width: .5em;
    @include center;
    position: relative;
    svg{
      width: 100%;
    }
  }
  &__hidden{
    margin-top: .625em;
    display: none;
  }
  &__radios{
    display: flex;
    flex-wrap: wrap;
    .m-radio{
      margin-right: 1em;
    }
    .m-radio2{
      margin: .3125em .3125em 0 0;
    }
  }
}

.m-tooltip{
  position: relative;
  &:hover{
    .m-tooltip__wrap{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__wrap{
    position: absolute;
    right: -3.875em;
    top: 100%;
    z-index: 5;
    width: 17.375em;
    padding-top: .5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__bg{
    width: 100%;
    display: block;
  }
  &__inner{
    position: relative;
    height: 18.125em;
    overflow: hidden;
  }
  &__text{
    position: absolute;
    top: 0;
    left: 0;
    padding: 2em 1em;
    color: #fff;
    font-weight: 500;
    .text14{
      line-height: 150%;
    }
  }
}

.m-radio2{
  input:checked~label{
    background-color: #fff;
    color: $primary;
  }
  label{
    height: 2.5em;
    display: flex;
    align-items: center;
    padding: 0 1.0625em;
    border: 1px solid #e0e0e0;
    border-radius: .75em;
    font-weight: 700;
    color: #606060;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .quiz-manufacture{
    &__wrap{
      grid-template-columns: 100%;
      margin-top: 2em;
    }
    &__col{
      order: 2;
    }
  }

  .manufactures{
    max-height: unset;
    padding: 0;
    &__items{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
      gap: .625em;
    }
  }

  .manufactures-head{
    display: none;
  }

  .manufacture-item{
    input:checked{
      &~.manufacture-item__grid{
        .manufacture-item__img{
          border-color: $yellow;
          &:before{
            opacity: 1;
          }
        }
      }
    }
    &__grid{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__col{
      border: none;
      padding: 0;
      width: 100%;
      justify-content: center;
    }
    &__col2,
    &__col3{
      display: none;
    }
    &__price{
      font-size: 1em;
    }
    &__img{
      width: 100%;
      height: 3em;
      padding: .75em;
      background: #fff;
      border-radius: .5625em;
      border: 1px solid #d3d3d3;
      transition: all .3s;
      position: relative;
      justify-content: center;
      &:before{
        content: '';
        position: absolute;
        top: .375em;
        right: .375em;
        width: .5em;
        height: .5em;
        border-radius: 50%;
        background-color: $yellow;
        transition: inherit;
        opacity: 0;
      }
    }
  }

  .manufacture-props{
    gap: .25em;
  }

  .manufacture-prop{
    border: 1px solid $black;
    border-radius: .5625em;
    padding: .5em;
    .m-tooltip{
      display: none;
    }
    &__icon{
      width: 1em;
      margin-right: .375em;
    }
    &__title{
      font-size: .875em;
      br{
        display: none;
      }
    }
    &__hidden{
      margin-top: .5em;
    }
  }
}
