.lifehack-page{
  padding: 2.75em 0 5em;
  &__desc{
    max-width: 41.7em;
  }
}

.advices{
  margin-top: 3.75em;
  &__grid{
    margin-top: 1.125em;
    display: grid;
    grid-template-columns: 28.3125em calc(100% - 30.3125em);
    gap: 2.5em 2em;
  }
}

.advices-hint{
  height: 23.875em;
  position: sticky;
  top: 1em;
  padding: 5.4375em 1.5em 1.875em 9.75em;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 153%;
    font-weight: 500;
    font-family: $font2;
  }
  &__img2{
    position: absolute;
    width: 18.4375em;
    top: -1.625em;
    left: -3.625em;
  }
}

.advices-slider{
  margin-top: 3em;
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        @include grid100gap(.6875em);
        transform: unset !important;
        max-height: 46em;
        overflow-y: auto;
        padding-right: .5em;
        &::-webkit-scrollbar {
          width: 0.3125em;
          height: 0.3125em;
        }
        &::-webkit-scrollbar-track {
          background: #EDEDED;
          border-radius: 0;
        }
        &::-webkit-scrollbar-thumb {
          background: $primary;
        }
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.advice-card{
  padding: 2.25em 2.375em 2.25em 6.125em;
  background-color: #fff;
  border-radius: 2.5em;
  border: 1px solid $primary;
  position: relative;
  &__num{
    position: absolute;
    width: 4.25em;
    height: 3.9375em;
    border-radius: 2.5em 0;
    @include center;
    background-color: $primary;
    left: -1px;
    top: 0;
    color: #fff;
    span{
      opacity: .5;
    }
  }
  &__desc{
    margin-top: 1.1875em;
    color: #6d6d6d;
    font-size: 1em;
    line-height: 150%;
  }
}


@media screen and (max-width: 992px){
  .lifehack-page{
    padding: 1.5em 0 2em;
    &__desc{
      margin-top: 1em;
    }
  }

  .advices{
    margin-top: 2.125em;
  }

  .advices{
    margin-top: 3.75em;
    &__grid{
      grid-template-columns: 21.5625em calc(100% - 23.5625em);
    }
  }

  .advices-hint{
    height: 23.9375em;
    padding: 5.4375em .9375em 1.875em 3.5em;
    &__img{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
    &__img2{
      left: -6.375em;
      width: 15.4375em;
    }
  }

  .advices-slider{
    margin-top: 1.25em;
    @media screen and (min-width: 577px){
      .swiper{
        &-wrapper{
          gap: 1.75em;
        }
      }
    }
  }

  .advice-card{
    border-radius: 1.25em;
    height: 100%;
    padding: 2.625em .9375em;
    &__num{
      left: .9375em;
      top: -1.1875em;
      width: 2.375em;
      height: 2.375em;
      border-radius: .5em;
      .h3{
        font-size: 1em;
      }
    }
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: .875em;
      margin-top: .57em;
    }
  }
}


@media screen and (max-width: 576px){
  .lifehack-page{
    overflow: hidden;
  }

  .advices{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      order: 2;
    }
  }

  .advices-slider{
    .swiper{
      &-container{
        width: calc(100% + 1.5em);
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        width: 100%;
        height: auto;
        padding-right: 1.25em;
      }
      &-pagination{
        position: static;
        margin-top: 1.75em;
      }
    }
  }
}
