.quiz-count{
  &__wrap{
    margin-top: 5.3125em;
    display: grid;
    grid-template-columns: 27.5em 20.1875em;
    gap: 1.5em;
    justify-content: space-between;
  }
  &__size{
    .range-field__top{
      font-weight: 600;
      font-family: $font2;
      font-size: .875em;
    }
    .range-field__val{
      font-size: 1.71em;
    }
  }
  &__desc{
    margin-top: .3125em;
    line-height: 120%;
  }
  &__nav{
    display: flex;
    margin-top: 1.4375em;
  }
  &__link{
    height: 3em;
    font-weight: 600;
    padding: 0 1.375em;
    span{
      font-size: 1.125em;
    }
    img{
      width: .75em;
      margin-right: .3125em;
    }
  }
}

.range-field{
  &__wrap{
    padding: 0 .125em;
  }
  .range-slider{
    width: 100%;
    background-color: #e2e2e2;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    margin-top: 1.125em;
    .noUi-connect{
      background-color: $black;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $black;
      width: .75em;
      height: .75em;
      top: -.3125em !important;
      right: -.3125em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 200%;
        height: 200%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
}

.quiz-where{
  &__wrap{
    display: grid;
    grid-template-columns: 33.75em 27.5em;
    justify-content: space-between;
    gap: 1.5em;
  }
  &__item{
    @include lastM(0 0 1.875em);
    .range-field{
      margin-top: 1em;
      &__from,
      &__to,
      &__val{
        font-size: 1.25em;
        font-weight: 600;
      }
      &__val{
        display: none;
      }
      &__top{
        display: flex;
        justify-content: space-between;
        color: #6a6a6a;
      }
    }
  }
  &__check{
    margin-bottom: 1.875em;
    margin-left: -2.125em;
    label{
      align-items: flex-start;
      padding-left: 2.125em;
      span{
        font-size: 1.125em;
      }
      &:before{
        top: 0;
        transform: unset;
        width: 1.25em;
        height: 1.25em;
      }
    }
  }
  &__search{
    position: relative;
    &-btn{
      position: absolute;
      top: 50%;
      right: 1em;
      transform: translateY(-50%);
      width: 1.6875em;
      @include center;
      svg{
        width: 100%;
      }
    }
  }
  &__map{
    margin-top: 1.375em;
    display: flex;
  }
  &__btn{
    display: flex;
    align-items: center;
    font-weight: 600;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1.5em;
      margin-right: .5625em;
    }
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .quiz-count{
    &__wrap{
      margin-top: 0;
      grid-template-columns: 100%;
    }
    &__col2{
      padding: 1.25em 2em 1.75em;
      background-color: #fff;
      border-radius: .5625em;
      .h3{
        font-size: 1.125em;
      }
    }
    &__nav{
      margin-top: 1em;
    }
    &__link{
      width: 100%;
    }
  }

  .quiz-where{
    &__wrap{
      grid-template-columns: 100%;
    }
    &__check{
      margin-left: 0;
      label {
        span {
          font-size: 1em;
        }
      }
    }
    &__item{
      .text18{
        font-size: 1em;
      }
      .range-field{
        &__from,
        &__to{
          font-size: 1em;
        }
      }
      .range-slider{
        margin-top: .75em;
      }
    }
    &__map{
      margin-top: .875em;
    }
    &__btn{
      color: $primary;
      path{
        fill: $primary;
      }
      svg{
        width: 1.125em;
        margin-right: .125em;
      }
      span{
        font-size: 1em;
      }
    }
  }
}
