.order-detail,
.order-prices{
  &__title,
  &__desc2{
    font-size: 1em;
    line-height: 119%;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 700;
  }
}

.order-detail{
  display: flex;
  justify-content: space-between;
  padding-top: 1.25em;
  border-top: 1px dashed #e6e6e6;
  &__items{
    display: flex;
    align-items: flex-start;
  }
  &__item{
    @include lastM(0 1.875em 0 0);
    @include grid100gap(.4375em);
  }
  &__title{
    ._view2{
      display: none;
    }
  }
  &__edit{
    display: flex;
    align-items: center;
    font-family: $font2;
    font-weight: 700;
    text-align: left;
    &:hover{
      color: $primary;
      i{
        background-color: $primary;
      }
    }
    i{
      transition: all .3s;
      background-color: $black;
      width: 1.9375em;
      height: 1.9375em;
      @include center;
      border-radius: 50%;
      flex-shrink: 0;
      margin-right: .6875em;
    }
    img{
      width: 1em;
    }
  }
}

.order-detail2{
  border-top: 1px dashed #e6e6e6;
  margin-top: 1em;
  padding-top: 1.25em;
  display: flex;
  align-items: center;
}

.order-prices{
  display: flex;
  &__items{
    display: flex;
  }
  &__item{
    margin-right: 1.875em;
  }
  &__desc{
    margin-top: .125em;
  }
}

.order-bonus{
  display: flex;
  align-items: center;
  height: 2.8125em;
  padding: 0 1.25em;
  background: #f6f6f6;
  border-radius: .75em;
  color: #727272;
  font-weight: 500;
  margin-left: .625em;
  img{
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    width: 1.5625em;
    border-radius: 50%;
    margin-right: .5em;
  }
}

.order-download{
  margin-left: auto;
  display: flex;
  align-items: center;
  font-weight: 700;
  color: $primary;
  font-family: $font2;
  span{
    font-size: 1em;
  }
  svg{
    width: 1.5em;
    margin-right: .4375em;
  }
}

.order-nav{
  display: flex;
  align-items: center;
  margin-top: 1em;
  padding-top: 1.125em;
  border-top: 1px dashed #e6e6e6;
  &__btn{
    padding: 0 .75em;
    width: 16.6875em;
    height: 3.1875em;
    border-radius: .75em;
    font-weight: 600;
    font-family: $font2;
    margin-right: .3125em;
  }
}

.order-review{
  display: flex;
  align-items: center;
  font-weight: 700;
  font-family: $font2;
  margin-left: 1.25em;
  i{
    width: 1.9375em;
    height: 1.9375em;
    background-color: $primary;
    border-radius: 50%;
    flex-shrink: 0;
    @include center;
    margin-right: .6875em;
  }
  img{
    width: 1em;
  }
}

.order-filter{
  padding: 1.25em 2.5em 2.8125em;
  background: #eaeaea;
  border-radius: .75em;
  margin-top: 2.625em;
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1em;
  }
  &__fields{
    display: grid;
    grid-template-columns: 8.125em 14.5em 19.625em 6.625em;
    gap: .75em;
    .fg{
      label{
        font-size: 1em;
        line-height: 125%;
        font-weight: 600;
        margin-bottom: .3125em;
      }
      input{
        padding: 0 1.25em;
        border: 1px solid #979797;
        height: 2.5em;
      }
    }
  }
  &__nav{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__confirm{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-family: $font2;
    &:hover{
      color: $yellow;
    }
    span{
      font-size: 1em;
    }
    img{
      width: 1.9375em;
      margin-right: .6875em;
    }
  }
  &__refresh{
    font-size: 1em;
    line-height: 125%;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #b0b0b0;
    font-weight: 500;
    margin-top: .5em;
    &:hover{
      color: $yellow;
    }
  }
  &__top{
    display: flex;
    .fg{
      margin-right: 2.25em;
    }
    .nice-select{
      background-color: transparent !important;
      height: auto;
      padding: 0;
      border: none !important;
      &:before{
        background-image: url(../img/select-arrow3.svg);
        margin-left: .4375em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .order-detail{
    display: block;
    &__items{
      @include grid100gap(.75em);
    }
    &__item{
      display: grid;
      grid-template-columns: 9em calc(100% - 9em);
      align-items: flex-start;
    }
    &__desc{
      font-size: 1em;
      line-height: 119%;
    }
    &__title{
      font-weight: 600;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__desc,
    &__desc{
      font-weight: 400;
    }
    &__edit{
      margin-top: .6875em;
      br{
        display: none;
      }
    }
  }

  .order-detail2{
    flex-direction: column;
    align-items: flex-start;
  }

  .order-prices{
    width: 100%;
  }

  .order-bonus{
    margin: .625em 0 0;
  }

  .order-download{
    margin: .875em 0 0;
  }

  .order-nav{
    margin-top: 1em;
    padding-top: 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    &__btn{
      margin: 0;
      width: 100%;
    }
  }

  .order-review{
    margin: .8125em 0 0;
  }

  .order-filter{
    padding: 1.375em .9375em 1.625em;
    &__bottom{
      display: block;
      margin-top: 1.125em;
    }
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .3125em / 2));
      gap: .3125em;
    }
    &__nav{
      display: grid;
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .3125em / 2));
      gap: 1em .3125em;
      align-items: center;
    }
    &__confirm{
      justify-content: center;
      height: 3.125em;
      border-radius: .75em;
      width: 100%;
      background-color: $yellow;
      img{
        display: none;
      }
    }
    &__refresh{
      margin: 0;
    }
    &__top{
      flex-wrap: wrap;
      margin-top: -.5em;
      width: calc(100% + 1em);
      .fg{
        margin: .5em .5em 0 0;
      }
      .nice-select{
        .current{
          font-size: .9375em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .order-detail{
    &__item{
      grid-template-columns: 7.5em calc(100% - 7.5em);
    }
    &__title,
    &__desc,
    &__desc2{
      font-size: .875em;
    }
    &__edit{
      i{
        width: 1.5625em;
        height: 1.5625em;
        margin-right: .5em;
      }
      img{
        width: .75em;
      }
      span{
        font-size: .875em;
      }
    }
  }

  .order-prices{
    justify-content: space-between;
    &__items{
      display: block;
    }
    &__item{
      margin: 0 0 .625em;
      min-width: 5.875em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    &__title{
      font-size: .875em;
    }
    &__desc{
      font-size: 1.125em;
    }
  }

  .order-bonus{
    padding: 0 .5em;
    height: 1.9375em;
    img{
      width: 1.25em;
      margin-right: .375em;
    }
  }

  .order-download{
    color: $black;
    i{
      width: 1.5625em;
      height: 1.5625em;
      border-radius: 50%;
      @include center;
      background-color: $primary;
      margin-right: .5em;
    }
    svg{
      width: 1.25em;
      margin: 0;
    }
    path{
      fill: #fff;
    }
    span{
      font-size: .875em;
    }
  }

  .order-nav{
    grid-template-columns: 100%;
  }

  .order-review{
    justify-content: center;
  }

  .order-filter{
    &__fields{
      grid-template-columns: 100%;
      .fg{
        label{
          display: none;
        }
      }
    }
    &__nav{
      grid-template-columns: 100%;
    }
  }
}
