.calc{
  padding: 2.5em 0 5em;
  &__desc{
    font-size: 1em;
    line-height: 164%;
    color: #737373;
    max-width: 47.5em;
    margin-top: 2.5em;
  }
}

.calc-process{
  margin-top: 3.75em;
  padding: 3.75em 3.5em 2.375em;
  background: #fff;
  border-radius: 2.5em;
  &__variants{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.375em / 4));
    gap: 1.125em;
  }
  &__bottom{
    margin-top: 2em;
    padding-top: 2.25em;
    border-top: 1px solid #ebebeb;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__btn{
    padding: 0 2.6875em;
    font-weight: 700;
  }
}

.calc-loss{
  margin-top: 1.625em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__top{
    display: flex;
    align-items: center;
    font-weight: 700;
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
  &__items{
    margin-top: .75em;
    @include grid100gap(.5em);
  }
}

.calc-info{
  @include grid100gap(.5625em);
  max-width: 12.75em;
  &__title{
    font-size: 1em;
    line-height: 119%;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 600;
  }
  &__price{
    font-size: 1.875em;
    line-height: 123%;
    font-weight: 700;
    color: $primary;
    margin-top: -.167em;
  }
  &__btn{
    display: inline-flex;
    align-items: center;
    margin: -.3125em auto 0 0;
    font-weight: 700;
    font-family: $font2;
    &:hover{
      color: $primary;
    }
    span{
      font-size: 1em;
    }
    img{
      width: 1.9375em;
      margin-left: .625em;
    }
  }
}

.calc-variant{
  display: none;
  margin-top: 2.75em;
  &._active{
    display: block;
  }
  &__wrap{
    display: inline-flex;
    position: relative;
    .fg{
      .nice-select,
      input{
        box-shadow: 0 8px 14px 0 rgba(0, 0, 0, 0.13);
      }
      label{
        margin-bottom: .3125em;
        font-size: 1em;
        line-height: 125%;
        font-weight: 700;
      }
      @media screen and (min-width: 993px){
        margin-right: 1.5em;
        width: 8.75em;
        &--big{
          width: 16em;
        }
        &--float{
          position: absolute;
          top: 100%;
          right: 0;
          margin-top: 1.5em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .calc{
    padding: 1.5em 0 2.25em;
    &__desc{
      margin-top: 2.125em;
    }
  }

  .calc-loss{
    margin-top: 2em;
  }

  .calc-process{
    margin-top: 3em;
    padding: 1.5em .9375em 2.25em;
    border-radius: 1.25em;
    &__variants{
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      gap: .5em;
    }
    &__bottom{
      margin-top: 1.75em;
      padding-top: 1.875em;
      @include grid100gap(1.125em);
    }
    &__btn{
      margin: 0 0 .875em;
      padding: 0;
    }
  }

  .calc-variant{
    margin-top: 1.25em;
    &__wrap{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .calc-process{
    &__variants{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
  }

  .calc-variant{
    &__wrap{
      grid-template-columns: 100%;
    }
  }
}
