.lie{
  padding: 2.75em 0 5em;
}

.lie-main{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 600;
  font-family: $font2;
  position: relative;
  &__smile{
    position: absolute;
    top: -1.25em;
    left: 2.125em;
    width: 9.25em;
    transform: rotate(-25deg);
  }
  &__smile2{
    position: absolute;
    right: 2em;
    bottom: -5.25em;
    transform: rotate(32deg);
    width: 6.25em;
  }
  &__top{
    max-width: 40em;
  }
  &__title{
    font-size: 1.25em;
    line-height: 120%;
  }
  &__arrow{
    margin: 1.625em 0 1em;
    width: 1em;
  }
  &__title2{
    font-size: 1.875em;
    line-height: 123%;
  }
}

.lie-hint{
  margin-top: 3em;
  border: .4375em solid #fff;
  border-radius: 2.5em;
  padding: 1.5em 1.875em;
  display: flex;
  align-items: center;
  &__img{
    width: 12.5em;
    margin-right: 2.75em;
  }
  &__title{
    font-size: 1.25em;
    line-height: 151%;
    font-weight: 700;
    color: $primary;
  }
  &__desc{
    font-size: 1em;
    line-height: 167%;
    font-weight: 500;
    margin-top: 1.125em;
  }
}


@media screen and (max-width: 992px){
  .lie{
    padding: 1.5em 0 2em;
    overflow: hidden;
    .service-slider{
      margin-top: 3.75em;
    }
  }

  .lie-main{
    margin-top: 1.5em;
    &__smile{
      top: 5.75em;
      left: .1875em;
      width: 3.75em;
      transform: rotate(32deg);
    }
    &__smile2{
      right: -.1875em;
      bottom: 4.5em;
      transform: rotate(-29deg);
      width: 3.25em;
    }
    &__title{
      font-size: 1em;
    }
    &__arrow{
      margin: 1.25em 0;
    }
    &__title2{
      font-size: 1.25em;
    }
  }

  .lie-hint{
    padding: 4em 1.25em 1.625em;
    margin-top: 3.125em;
    position: relative;
    &__img{
      width: 7.5em;
      position: absolute;
      top: -3em;
      left: .85em;
    }
    &__title{
      font-size: 1.125em;
      line-height: 147%;
    }
    &__desc{
      font-size: .875em;
      margin-top: .65em;
    }
  }
}


@media screen and (max-width: 576px){
  .lie-main{
    &__title{
      max-width: 19.375em;
    }
  }

  .service-slider2{
    font-size: 1em;
  }
}
