.quiz-when{
  &__top{
    display: flex;
  }
  &__radios{
    display: flex;
  }
  &__radio{
    @include lastM(0 .3125em 0 0);
    input:checked~label{
      background: linear-gradient(162deg, #000 0%, #606060 100%);
      color: #fff;
    }
    label{
      padding: 0 2.375em;
      height: 3.125em;
      @include center;
      border-radius: .75em;
      border: 1px solid #757575;
      font-weight: 700;
      span{
        font-size: 1em;
      }
    }
  }
  &__right{
    padding: .6125em 0 0 1em;
    flex-grow: 1;
  }
  &__time{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 2.625em;
  }
}

.time-radio{
  margin-top: 1.875em;
  display: flex;
  &__item{
    @include lastM(0 .375em 0 0);
    input:checked~label{
      background: linear-gradient(162deg, #000 0%, #606060 100%);
      color: #fff;
    }
    label{
      padding: 0 1.0625em;
      height: 2.5em;
      @include center;
      border-radius: .75em;
      border: 1px solid #e0e0e0;
      font-weight: 700;
      color: #606060;
      span{
        font-size: 1em;
      }
    }
  }
}

.order-part{
  @include grid100gap(.8125em);
  display: none;
  &._active{
    display: grid;
  }
  &__item{
    display: flex;
    position: relative;
  }
  &__clear{
    position: absolute;
    width: .4375em;
    @include center;
    left: 100%;
    bottom: 100%;
    svg{
      width: 100%;
    }
  }
  &__date{
    width: 5.125em;
    height: 2.625em;
    background: #fff;
    border-radius: .5625em;
    @include center;
    font-weight: 600;
    color: $primary;
    margin-right: .1875em;
    span{
      font-size: 1em;
    }
  }
  &__select{
    width: 4.5em;
    .fg{
      .nice-select{
        height: 2.625em;
        font-size: 1em;
        box-shadow: unset;
        padding: 0 .5em 0 .75em;
        font-weight: 600;
        border-radius: .5625em;
        &:before{
          background-image: url(../img/select-arrow2.svg);
          width: .4375em;
          height: .3125em;
          margin-left: .25em;
        }
        .list{
          max-height: 6.75em;
        }
      }
    }
  }
}

.quiz-calendar{
  margin-top: -1.875em;
  width: 15.75em;
  display: none;
  &._active{
    display: block;
  }
  .air-datepicker{
    width: 100%;
  }
}

.air-datepicker{
  box-sizing: border-box;
  font-family: $font3;
  font-size: 1em;
  background: #fff;
  border: none;
  border-radius: .75em;
  padding: .5em 1.375em 1em;
}
.air-datepicker-nav{
  border: none;
  padding: 0;
  align-items: center;
  justify-content: center;
  &--title{
    font-size: 1em;
    letter-spacing: 0.01em;
    color: #575757;
    font-weight: 600;
  }
}
.air-datepicker-body--day-names,
.air-datepicker-body--cells.-days-{
  grid-template-columns: repeat(7,calc(100% / 7));
  grid-auto-rows: unset;
}
.air-datepicker-body--day-names{
  background: #e6e6e6;
  margin: .25em 0;
  height: 1.5em;
}
.air-datepicker-body--day-name{
  font-size: .6875em;
  letter-spacing: 0.01em;
  color: #575757;
  text-transform: capitalize;
}
.air-datepicker--content{
  padding: 0;
}
.air-datepicker-cell{
  font-size: .75em;
  width: 100%;
  height: 2.43em;
  letter-spacing: 0.01em;
  &.-focus-{
    background-color: $primary;
    color: #fff;
  }
  &.-current-{
    background-color: rgba($primary,.13);
    color: $primary;
  }
}

.mob-calendar{
  display: none;
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .quiz-when{
    &__top{
      display: block;
    }
    &__radios{
      display: block;
    }
    &__radio{
      @include lastM(0 0 .4375em);
      label{
        width: 100%;
        height: 2.3125em;
      }
    }
    &__right{
      padding: 1.375em 0 0;
    }
    &__time{
      flex-direction: column;
      margin-top: .625em;
    }
  }

  .time-radio{
    width: calc(100% + 1.875em);
    margin: 1em 0 0 -.9375em;
    padding: 0 .9375em;
    overflow-x: auto;
    @include nullScroll;
    &__item{
      flex-shrink: 0;
    }
  }

  .order-part{
    width: 100%;
    &__clear{
      position: static;
      margin-left: auto;
      width: .875em;
    }
  }

  .quiz-calendar{
    display: none;
  }

  .mob-calendar{
    height: 13.75em;
    overflow: hidden;
    display: none;
    align-items: center;
    width: 100%;
    margin-bottom: 1.25em;
    position: relative;
    &._active{
      display: flex;
    }
    &:before,
    &:after{
      content: '';
      position: absolute;
      height: 5em;
      width: 100%;
      left: 0;
      z-index: 2;
    }
    &:before{
      top: 0;
      background-image: linear-gradient(to bottom,#f1f1f1,rgba(246,246,246,0));
    }
    &:after{
      bottom: 0;
      background-image: linear-gradient(to top,#f1f1f1,rgba(246,246,246,0));
    }
    &__wrap{
      display: flex;
      position: relative;
    }
  }

  .calendar-slider{
    margin-right: .875em;
    &._day{
      width: 2.625em;
    }
    &._month{
      width: 5.125em;
    }
    &._year{
      width: 4.125em;
    }
    .swiper{
      &-container{
        height: 2.125em;
        border-top: 1px solid #cbcbcb;
        border-bottom: 1px solid #cbcbcb;
      }
      &-slide{
        @include center;
        &-active{
          .calendar-slider__item{
            color: $black;
          }
        }
      }
    }
    &__item{
      transition: all .3s;
      text-align: center;
      font-weight: 500;
      color: #a6a6a6;
      span{
        font-size: 1.125em;
      }
    }
  }
}
