.all-inclusive{
  padding: 2.75em 0 5em;
  .service-slider{
    margin-top: 1.625em;
    @media screen and (min-width: 577px){
      .swiper{
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(2,calc(50% - .8125em));
          gap: 1.625em;
          transform: unset !important;
        }
        &-slide{
          &:nth-child(1){
            transform: translateY(9em);
          }
          &:nth-child(2){
            .service-card{
              &__img{
                transform: translate(-30%,-55%);
              }
            }
          }
          &:nth-child(3){
            grid-column: 2/3;
            .service-card{
              &__img{
                transform: translate(-10%,-85%);
              }
            }
          }
        }
        &-pagination{
          display: none;
        }
      }
    }
  }
}

.lie{
  .service-slider{
    margin-top: 12.25em;
    @media screen and (min-width: 577px){
      .swiper{
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(2,calc(50% - .8125em));
          gap: 1.625em;
          transform: unset !important;
        }
        &-slide{
          &:nth-child(odd){
            transform: translateY(-6em);
          }
        }
        &-pagination{
          display: none;
        }
      }
    }
  }
}

.all-main{
  padding: 4em 4.625em 2em 33.125em;
  display: flex;
  flex-direction: column;
  margin-top: 2.75em;
  &__subtitle{
    font-size: 1.5em;
    line-height: 151%;
    font-weight: 600;
    max-width: 18em;
  }
  &__title{
    font-size: 4.375em;
    line-height: 120%;
    font-weight: 600;
    font-family: $font2;
  }
  &__desc{
    padding-top: 1.5em;
    margin-top: auto;
    font-size: 1.125em;
    line-height: 167%;
  }
  &__img{
    position: absolute;
    width: 35em;
    bottom: 2.875em;
    left: -5.25em;
  }
}

.we-get{
  margin-top: 3.75em;
  &__inner{
    position: relative;
    padding-bottom: 5.75em;
  }
}


.service-card{
  padding: 3.25em 2.25em 3.25em 11em;
  background-color: #fff;
  border-radius: 2.5em;
  position: relative;
  height: 100%;
  &__desc{
    font-size: 1em;
    line-height: 150%;
    color: #6d6d6d;
    margin-top: 1.25em;
  }
  &__img{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-30%,-65%);
    max-width: 20.125em;
    max-height: 29.125em;
  }
}

.service-card2{
  width: calc(50% - .8125em);
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 2.25em 2em 3.5em 2.375em;
  border: .4375em solid #fff;
  border-radius: 2.5em;
  &__top{
    display: flex;
    align-items: center;
    img{
      width: 3.3125em;
      margin-right: 1.375em;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 147%;
    font-weight: 700;
    color: $primary;
  }
  &__desc{
    font-size: 1em;
    line-height: 167%;
    font-weight: 500;
    margin-top: 1.625em;
  }
}


@media screen and (max-width: 992px){
  .all-inclusive{
    padding: 1.5em 0 2em;
  }

  .all-main{
    &__title{
      font-size: 3.25em;
    }
  }

  .we-get{
    &__inner{
      font-size: .6875em;
    }
  }

  .service-slider2{
    font-size: .6875em;
  }
}


@media screen and (max-width: 576px){
  .all-inclusive{
    overflow: hidden;
  }

  .all-main{
    padding: 2.875em 1.875em 9.1875em;
    gap: unset;
    margin-top: 2.125em;
    &:before{
      width: 11.0625em;
      height: 11.0625em;
      bottom: -3.625em;
      left: -2.4375em;
    }
    &__subtitle{
      font-size: 1em;
    }
    &__title{
      font-size: 1.875em;
      line-height: 137%;
      margin-top: .33em;
    }
    &__desc{
      font-size: .875em;
    }
    &__img{
      bottom: 0;
      width: 18.3125em;
      left: 1.875em;
    }
  }

  .we-get{
    margin-top: 2.125em;
    &__inner{
      font-size: 1em;
    }
  }

  .service-slider{
    margin-top: 2.3125em;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        transition: all .3s;
        &:not(.swiper-slide-active){
          opacity: 0;
          pointer-events: none;
        }
      }
      &-pagination{
        position: static;
        margin-top: 1.625em;
      }
    }
  }

  .service-card{
    padding: 2.5em .9375em;
    border-radius: 1.25em;
    &__img{
      max-width: 9.75em;
      max-height: 6.625em;
      top: -1.875em;
      right: -2em;
      left: unset;
      transform: unset;
    }
    .h3{
      font-size: 1.25em;
    }
    &__desc{
      margin-top: 2.14em;
      font-size: .875em;
      line-height: 150%;
    }
  }

  .service-card2{
    display: none;
  }
}
