.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1em;
    line-height: 167%;
    margin-bottom: .1875em;
    color: #787878;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0.75em;
    border: 1px solid #e7e7e7;
    padding: 0 1.25em;
    height: 3.75em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: #000;
    font-weight: 500;
    &::placeholder {
      color: #979797;
    }
    &:focus {
      border-color: $yellow;
    }
  }
  textarea{
    padding-top: 1em;
    padding-bottom: 1em;
    height: 9.875em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    white-space: nowrap;
    padding: 0 .75em;
    float: unset;
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .625em;
      height: .625em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      border: 1px solid #efefef;
      margin-top: .25em;
      border-radius: .75em;
      padding: .25em .75em;
      background: #FFF;
      box-shadow: none;
      max-height: 15.5em;
      overflow-y: auto;
      z-index: 5;
      width: auto;
      min-width: 100%;
      @include scroll;
    }
    .option{
      height: auto;
      font-size: 1em;
      line-height: 118%;
      padding: .4375em 0;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      border-bottom: 1px solid rgba($black,.05);
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: transparent;
        color: $primary;
        font-weight: 400;
      }
    }
  }
  &--shadow{
    .nice-select,
    input{
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.05);
      border-color: #fff;
      background-color: #fff;
      font-size: 1.125em;
      height: 3em;
      padding: 0 1.33em;
    }
  }
  &._type2{
    input{
      height: 3em;
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.file-input,
.time-radio__item,
.quiz-when__radio,
.m-radio2,
.manufacture-item,
.variant-field,
.transport-field,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 1.8125em;
    min-height: 1.3125em;
    span{
      font-size: 1em;
      line-height: 119%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid $black;
      width: 1.3125em;
      height: 1.3125em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check{
  input:checked~label{
    &:before{
      background-size: .75em auto;
    }
  }
  label{
    font-weight: 600;
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.0625em;
    min-height: 1.5em;
    span{
      font-size: 1em;
      line-height: 125%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 1.5em;
      height: 1.5em;
      background-color: #fff;
      border-radius: .25em;
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.09);
      @include bgImgSize(0);
      transition: all .0s;
      background-image: url(../img/check2.svg);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
  }
  &--red{
    label{
      &:before{
        background-image: url(../img/radio2.svg);
      }
    }
  }
}

.cartcalc{
  position: relative;
  .ccalc-minus,
  .ccalc-plus{
    position: absolute;
    right: .625em;
    width: .5em;
    @include center;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 200%;
      height: 200%;
    }
    svg{
      width: 100%;
    }
  }
  .ccalc-minus{
    top: 1em;
  }
  .ccalc-plus{
    bottom: 1em;
  }
}

.file-input{
  font-weight: 600;
  font-family: $font2;
  &__wrap{
    display: flex;
    align-items: flex-start;
    span{
      font-size: 1em;
      padding-top: .375em;
    }
    img{
      width: 1.9375em;
      margin-right: .6875em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .fg{
    .nice-select,
    input{
      height: 3.125em;
    }
    &--shadow{
      input{
        font-size: .875em;
        padding: 0 1.43em;
        border-radius: .86em;
      }
    }
  }

  .cartcalc{
    .ccalc-minus{
      top: .75em;
    }
    .ccalc-plus{
      bottom: .75em;
    }
  }

  .file-input{
    &__wrap{
      span{
        font-size: .875em;
      }
      img{
        width: 1.5em;
        margin-right: .5em;
      }
    }
  }
}

