.quiz-wish{
  &__wrap{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.875em;
  }
  &__all{
    display: flex;
    align-items: flex-start;
    img{
      width: .75em;
      margin: -.3125em 0 0 .25em;
    }
  }
  &__start-up{
    margin-top: .5em;
  }
  &__water{
    margin-top: 1.25em;
  }
}

.quiz-car{
  display: flex;
  align-items: center;
  margin-top: 2.5em;
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 600;
  }
  &__items{
    margin-left: 1em;
    display: flex;
    .transport-field{
      margin-right: .4375em;
    }
  }
}

.quiz-car2{
  margin-top: 1.625em;
}

.quiz-car3{
  margin-top: 1.25em;
}

.quiz-time{
  margin-top: 1.25em;
  display: flex;
  align-items: center;
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 600;
  }
  &__desc{
    font-size: 1em;
    line-height: 125%;
  }
  .hour-field{
    margin: 0 1.125em;
  }
}

.hour-field{
  font-weight: 700;
  color: #606060;
  padding: 0 .5em;
  border: 1px solid #e0e0e0;
  border-radius: .75em;
  height: 2.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 6.5em;
  &__text{
    font-size: 1em;
  }
  &__nav{
    display: flex;
    flex-direction: column;
  }
  &__btn{
    width: .625em;
    height: .625em;
    @include center;
    margin: .125em 0;
    svg{
      width: .5em;
    }
  }
}

.quiz-order{
  position: relative;
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 16.375em) 15.375em;
    gap: 1em;
    margin-top: -1.375em;
    margin-bottom: 2em;
  }
  &__col{
    display: flex;
  }
  &__col2{
    padding-top: 3em;
  }
  &__btn{
    position: absolute;
    right: 0;
    bottom: 0;
    height: 3.5em;
    width: 15.3125em;
    background: linear-gradient(168deg, #e93333 0%, #ed5d5d 100%);
    color: #fff;
    font-weight: 600;
    &:hover{
      opacity: .8;
    }
  }
}

.quiz-product{
  position: relative;
  min-height: 3.375em;
  padding-left: 11.75em;
  padding-bottom: 1.5em;
  border-bottom: 1px solid #5f5f5f;
  &__img{
    position: absolute;
    width: 8.625em;
    height: 3.375em;
    border-radius: .5625em;
    background: rgba(255, 255, 255, 0.86);
    @include center;
    padding: .5em;
    left: 0;
    top: 0;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 600;
  }
  &__desc{
    font-size: 1em;
    line-height: 119%;
    color: #bebebe;
    margin-top: .75em;
  }
}

.quiz-nums{
  display: flex;
  justify-content: space-between;
  margin-top: 1.25em;
  &__col{
    display: flex;
  }
  &__col2{
    min-width: 15.375em;
    .quiz-nums__item{
      margin-right: 0 !important;
    }
  }
  &__item{
    @include lastM(0 3.125em 0 0);
  }
  &__title{
    font-size: 1em;
    line-height: 119%;
    color: #bebebe;
    margin-bottom: .75em;
  }
  &__price{
    font-size: 1.875em;
    line-height: 123%;
    font-weight: 700;
  }
  &__sum{
    margin-top: .3125em;
    .quiz-nums__title{
      color: #fff;
      font-weight: 700;
      margin-bottom: 0;
    }
    .quiz-nums__price{
      color: #f45959;
    }
  }
}

.quiz-bonus{
  width: 26.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .8125em));
  gap: 1.25em 1.625em;
  &__title{
    margin-bottom: .9em;
  }
  &__count{
    display: flex;
    align-items: center;
    height: 2.8125em;
    padding: 0 1.125em;
    background: rgba(255, 255, 255, 0.12);
    border-radius: .75em;
    color: #d6d6d6;
    font-weight: 500;
    img{
      width: 1.5625em;
      margin-right: .5em;
    }
  }
  .fg{
    input{
      font-size: 1.125em;
      padding: 0 .61em;
      height: 2.5em;
      border-radius: .67em;
      font-weight: 600;
      color: #fff;
      border: 1px solid rgba(255, 255, 255, 0.37);
      &::placeholder{
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
  &__btn{
    grid-column: 1/-1;
    height: 2.8125em;
    font-weight: 600;
    border-radius: .75em;
  }
  &__code{
    grid-column: 1/-1;
    &.fg{
      input{
        font-weight: 400;
      }
    }
    &-btn,
    &-hint{
      font-size: 1em;
      line-height: 119%;
    }
    &-hint{
      margin: .875em 0 .4375em;
    }
    &-btn{
      text-decoration: underline;
      color: #fff;
      &:hover{
        color: $yellow;
        text-decoration: none;
      }
    }
  }
}

.quiz-promo{
  margin-left: 3em;
  width: 16.5em;
  .fg{
    margin-top: .875em;
    input{
      padding: 0 1.5em;
      background-color: #fff;
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.05);
      height: 2.8125em;
    }
  }
}

.quiz-bonus2{
  display: flex;
  align-items: flex-start;
  img{
    width: 1.5625em;
    margin-right: .625em;
  }
  &__text{
    font-size: 1em;
    line-height: 120%;
    color: #bebebe;
    span{
      font-size: 1.125em;
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .quiz-wish{
    &__wrap{
      margin-top: .875em;
    }
    &__start-up{
      margin-top: 1.5em;
    }
  }

  .quiz-car{
    margin-top: 1.625em;
    display: block;
    width: 100%;
    &__items{
      margin: .375em 0 0 -.9375em;
      overflow-x: auto;
      width: calc(100% + 1.875em);
      padding: 0 .9375em;
      @include nullScroll;
    }
  }

  .quiz-car2{
    margin-top: 1.375em;
  }

  .quiz-car3{
    margin-top: 1.5625em;
  }

  .quiz-time{
    margin-top: 1.25em;
    flex-direction: column;
    align-items: flex-start;
    .hour-field{
      margin: .375em 0;
    }
  }

  .quiz-order{
    position: relative;
    padding-bottom: 10em;
    &__wrap{
      grid-template-columns: 100%;
      margin-bottom: 0;
      margin-top: 0;
      gap: 0;
    }
    &__col{
      flex-direction: column;
    }
    &__col2{
      order: -1;
      padding: 0;
    }
    &__btn{
      width: 100%;
    }
  }

  .quiz-product{
    padding-left: 0;
    padding-bottom: 1em;
    &__top{
      display: flex;
      align-items: center;
      margin-bottom: .3125em;
    }
    &__img{
      position: static;
      width: 6.375em;
      height: 2.5em;
      padding: .25em .5em;
      margin-right: .5em;
      flex-shrink: 0;
    }
    &__title{
      font-size: 1em;
    }
    &__desc{
      font-size: .875em;
      margin-top: 0;
    }
  }

  .quiz-nums{
    margin-top: 1em;
    &__col{
      flex-direction: column;
    }
    &__col2{
      min-width: 7em;
    }
    &__item{
      @include lastM(0 0 .5em);
    }
    &__title{
      font-size: .875em;
      margin-bottom: .25em;
    }
    &__price{
      font-size: 1.25em;
      line-height: 125%;
    }
    &__sum{
      position: absolute;
      left: 0;
      bottom: 5.25em;
      .quiz-nums__price{
        font-size: 1.875em;
      }
    }
  }

  .quiz-bonus{
    width: 100%;
    grid-template-columns: 7.5em calc(100% - 8.125em);
    gap: .75em .625em;
    padding-bottom: 1.5em;
    border-bottom: 1px solid #5f5f5f;
    &__title{
      font-size: 1em;
      margin-bottom: .5em;
    }
    &__count{
      padding: 0 .75em;
      .text18{
        line-height: 100%;
      }
    }
    &__code{
      margin-top: .625em;
      &-hint,
      &-btn{
        font-size: .875em;
      }
    }
  }

  .quiz-promo{
    order: -1;
    width: 100%;
    padding-bottom: 1.375em;
    border-bottom: 1px solid #5f5f5f;
    margin: 0 0 1.125em;
  }

  .quiz-bonus2{
    width: 100%;
    margin: 1em 0;
    padding-top: .75em;
    padding-bottom: 1em;
    border-bottom: 1px solid #5f5f5f;
    &__text{
      max-width: 12.5em;
    }
  }
}
