.order{
  .quiz-thank,
  .quiz{
    margin-top: 4.375em;
  }
}

.quiz{
  padding: 2.375em 3.375em 3.125em;
  border-radius: 2.5em;
  border: 1px solid #757575;
  display: flex;
  flex-direction: column;
  min-height: 43.625em;
  &._hide{
    display: none;
    &~.quiz-thank{
      display: flex;
    }
  }
  &._last{
    background: linear-gradient(143deg, #000 0%, #787878 100%);
    color: #fff;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.5em;
  }
}

.quiz-progress{
  display: flex;
  align-items: center;
  &__nums{
    font-size: 1.5em;
    font-weight: 600;
    font-family: $font2;
    color: #939393;
    span{
      font-size: 1.25em;
    }
  }
  &__bar{
    flex-grow: 1;
    margin-left: 1.5em;
    height: .625em;
    border-radius: .3125em;
    background-color: #fff;
    box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.07);
    span{
      display: block;
      height: 100%;
      border-radius: inherit;
      background-color: $yellow;
      max-width: 100%;
      transition: all .3s;
    }
  }
}

.quiz-nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1.875em;
  &__btn{
    padding: 0 1.5em;
    height: 2.75em;
    img{
      width: .9375em;
    }
    &._hide{
      opacity: 0;
      pointer-events: none;
    }
    &._prev{
      img{
        margin-right: .375em;
      }
    }
    &._next{
      img{
        margin-left: .375em;
      }
    }
  }
}

.quiz-step{
  display: none;
  &._active{
    display: block;
  }
  &__title{
    color: #939393;
    margin-bottom: 1.25em;
  }
  &__variants{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.375em / 4));
    gap: 1.125em;
  }
}

.variant-field{
  &:hover{
    z-index: 3;
  }
  &--black{
    .variant-field__inner{
      color: #fff;
      background: linear-gradient(144deg, #000 0%, #757575 100%);
    }
  }
  &._type2{
    input:not(:checked)~.variant-field__inner{
      border-color: #e9e9e9;
    }
  }
  input:checked~.variant-field__inner{
    border-color: $yellow;
    &:before{
      background-size: .875em auto;
    }
  }
  &__inner{
    padding: 2.75em 1.75em 2.1875em;
    height: 100%;
    min-height: 14.5625em;
    background: #fff;
    border: 1px solid #fff;
    border-radius: 2.5em;
    @include center;
    flex-direction: column;
    text-align: center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 1.125em;
      right: 1em;
      width: 2.375em;
      height: 2.375em;
      background-color: $yellow;
      border-radius: 50%;
      background-image: url(../img/check.svg);
      @include bgImgSize(0);
    }
  }
  &__img{
    height: 7em;
    @include center;
    margin-bottom: 1em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 600;
  }
  &__desc{
    color: #c3c3c3;
    font-size: .9375em;
    line-height: 120%;
    font-weight: 500;
    margin-top: .33em;
  }
  &__info{
    position: absolute;
    top: 1.75em;
    left: 1.75em;
    z-index: 5;
    .manufacture-prop__icon2{
      width: 1.25em;
      display: block;
    }
  }
}

.quiz-pay{
  &__change{
    margin-top: 1.25em;
    max-width: 33.75em;
    label{
      font-size: 1.125em;
      line-height: 122%;
      font-weight: 600;
      margin-bottom: .88em;
    }
  }
  &__hint{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
    b{
      font-weight: 600;
    }
    img{
      width: 2.3125em;
      flex-shrink: 0;
      margin-right: .5625em;
    }
  }
}

.quiz-thank{
  padding: 2.5em 2.5em 4.375em;
  box-shadow: 0 12px 19px 0 rgba(0, 0, 0, 0.11);
  background: linear-gradient(143deg, #000 0%, #787878 100%);
  border-radius: 2.5em;
  text-align: center;
  color: #fff;
  display: none;
  flex-direction: column;
  align-items: center;
  &__title{
    font-size: 1.875em;
    line-height: 122%;
    font-weight: 600;
    font-family: $font2;
  }
  &__desc{
    margin-top: .5em;
    font-size: 1em;
    line-height: 119%;
  }
  &__btn{
    margin-top: 2em;
    width: 23.125em;
  }
  &__img{
    margin: 2em auto 0;
    width: 38.625em;
    max-width: 100%;
  }
}


@media screen and (max-width: 992px){
  .order{
    padding: 1.25em 0 1.875em;
  }

  .quiz{
    font-size: .625em;
    padding: 1.5em;
    border-radius: 1.25em;
  }

  .variant-field{
    &._type2{
      .variant-field__inner{
        min-height: 6.625em;
        padding: .5em;
        border-radius: .5625em;
        &:before{
          display: none;
        }
      }
      .variant-field__img{
        height: 3.125em;
        margin-bottom: .25em;
      }
      .variant-field__info{
        display: none;
      }
      .variant-field__title{
        font-size: .875em;
        height: 121%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    .quiz-thank,
    .quiz{
      margin-top: 1.75em;
    }
  }

  .quiz{
    overflow: hidden;
    border: none;
    font-size: 1em;
    padding: .875em .9375em 2.5em;
    background: #f1f1f1;
    border-radius: .75em;min-height: unset;
  }

  .quiz-nav{
    margin-top: 1em;
  }

  .quiz-step{
    &__title{
      font-size: 1em;
    }
    &__variants{
      grid-template-columns: 100%;
      gap: .375em;
    }
  }

  .variant-field{
    &._type1{
      input:checked~.variant-field__inner{
        &:before{
          opacity: 1;
        }
      }
      &.variant-field--black{
        .variant-field__inner{
          justify-content: center;
        }
      }
      .variant-field__inner{
        padding: .5em .75em;
        min-height: 3.125em;
        flex-direction: row;
        justify-content: unset;
        border-radius: .5625em;
        &:before{
          background-image: unset;
          top: .3125em;
          right: .3125em;
          width: .5625em;
          height: .5625em;
          opacity: 0;
        }
      }
      .variant-field__img{
        width: 3.25em;
        height: 2em;
        margin: 0 .25em 0 0;
      }
      .variant-field__title{
        font-size: 1em;
      }
      .variant-field__desc{
        display: none;
      }
      .variant-field__info{
        display: none;
      }
    }
  }

  .quiz-pay{
    &__change{
      margin-top: 2.75em;
      label{
        font-size: 1em;
        margin-bottom: .375em;
      }
    }
  }

  .quiz-thank{
    padding: 3em .9375em;
    border-radius: .75em;
    &__title{
      font-size: 1.5em;
    }
    &__btn{
      width: 100%;
    }
    &__img{
      display: none;
    }
  }
}
